.about{
    background-color:#FFF2CC;
    padding:40px;
    text-align: center;
    display: grid;
    grid-template-columns: 40% 60%;
    grid-gap:15px;
}
.myImg{
    width: 100%;
}
.about div{
    padding:5px;
}
@media screen and (max-width:600px){
    .about{
        grid-template-columns: 100%;
    }
}