.skills{
   text-align: center;
   background-color: #F5F5F5;
   padding-top:40px;
   padding-bottom:40px;
}
.skills h1{
    margin-top:0;
}
.skills div{
    background-color:#D2DAFF;
    width:50%;
    margin-left:auto;
    margin-right:auto;
    padding: 15px;
    margin-top: 30px;
}
.skills p{
    display: inline-block;
    margin-right: 15px;
    border: 1px solid;
    padding: 12px;
    border-radius: 10px;
    background: white;
}