.projects{
    text-align: center;
    padding:40px 20px 40px 20px;
    background-color: #D5E8D4;
}
.container{
    display: grid;
    grid-template-columns: 33.3% 33.3% 33.3%;
    align-items: center;
    grid-gap:5px;
}
.container img{
    height:250px;
    width:85%;
    border: 2px solid;
}
.container button{
    background-color:#004225;
    color:white;
    border: none;
    padding: 10px;
    width: 100px;
    font-size: 1.1rem;
    border-radius: 8px;
}
.container button:hover{
    background-color:#77dd77;
    color:black;
}
@media screen and (max-width:600px){
    .container{
        grid-template-columns: 100%;
        grid-gap:30px;
    }
}