.NavBar{
  position: sticky;
  top: 0;
  z-index: 1;
  text-align: center;
  padding: 10px;
  background: #563c5c;
  display:relative;
  height: 35px;
}
.stickyNav{
  position: sticky;
}
.links a{
    margin-right: 25px;
    text-decoration: none;
    color:white;
    font-size: 1.2rem;
}
.navBtn{
  display: none;
}
.hamBurgerIcon{
  text-align: center;
  font-size: 25px;
  color:white;
}
@media screen and (max-width:600px){
  .navBtn{
    display:block;
    position: absolute;
    right: 20px;
  }
  .links{
    display:none;
  }
  .smallNavBar{
    height:100vh;
  }
  .smallNavBar .links{
    display: block;
    position: relative;
    top: 20%;
  }
  .smallNavBar .links a{
    display:block;
    margin-bottom: 20px;
  }
}