@import url('https://fonts.googleapis.com/css2?family=Allura&display=swap');
.home{
    height: 91vh;
    background-color: #E1D5E7;
    position: relative;
    text-align:center;
}
.box{
    position: absolute;
    top:40%;
    left:50%;
    -ms-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    font-size: 2rem;
}
.box p{
    margin-top: 0;
    margin-bottom: 0;
}
.box h1{
    font-size:5rem;
    margin-top:15px;
    font-family: 'Allura', cursive;
    margin-bottom: 10px;
}